"use client";

import { useEffect, useState } from "react";
import { useSearchParams } from "next/navigation";
import ChaptersDrawer from "../components/ChaptersDrawer";

const ChaptersPage = () => {
  const searchParams = useSearchParams();
  const selectedChapter = searchParams.get("chapter");
  const [activeChapter, setActiveChapter] = useState<string | null>(null);

  useEffect(() => {
    if (selectedChapter) {
      setActiveChapter(selectedChapter);
    }
  }, [selectedChapter]);

  return (
    <div className="flex">
      <ChaptersDrawer activeChapter={activeChapter} />
      <div className="flex-1 p-8 text-white">
        <h1 className="text-3xl font-bold">Chapter: {activeChapter}</h1>
        <p>Content of the selected chapter will go here...</p>
      </div>
    </div>
  );
};

export default ChaptersPage;
